<template>
  <div class="login">
    <div class="login-warp">
      <div class="login-form">
        <img class="login-form-img" src="../../assets/img/login/bg3.png" v-if="loginImgShow">
        <a-form class="login-form-in" layout="inline" @submit="handleSubmit" :style=" loginImgShow ? 'width:48%;' : 'width:100%;'">
          <span class="login-form-in-tit">登录账号</span>
          <a-form-item class="login-form-item">
            <a-input
              class="login-form-item-input"
              placeholder="输入用户名"
              v-model="values.userName"
            >
              <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
            </a-input>
          </a-form-item>
          <a-form-item class="login-form-item">
            <a-input
              class="login-form-item-input"
              type="password"
              placeholder="输入密码"
              v-model="values.password"
            >
              <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
            </a-input>
          </a-form-item>
          <a-form-item v-if="msg">
            <a-alert type="error" :message="msg" banner />
          </a-form-item>
          <a-form-item class="login-form-item">
            <a-button class="login-form-item-submit" type="primary" html-type="submit" :disabled="!values.userName || !values.password">
              登录
            </a-button>
          </a-form-item>
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
import {loginIn} from '../../common/js/checkLogin'
export default {
  name: 'login',
  data () {
    return {
      values: {
        userName: '',
        password: ''
      },
      loginImgShow: true,
      msg: ''
    }
  },
  mounted () {
    // 根据视口大小显隐图片
    this.getImgShow()
  },
  methods: {
    // Only show error after a field is touched.
    data () {
      return {
        msg: ''
      }
    },
    async login (params) {
      let res = await this.$Http.login(params)
      console.log(res)
      if (res.code === 0) {
        let that = this
        loginIn(res, function () {
          that.$router.replace({
            path: '/choiceStore',
            query: {
            }
          })
        })
      } else {
        this.msg = res.msg
      }
    },
    handleSubmit (e) {
      e.preventDefault()
      console.log('Received values of form: ', this.values)
      this.login(this.values)
    },
    getImgShow () {
      if (document.documentElement.clientWidth < 1050) {
        this.loginImgShow = false
      } else {
        this.loginImgShow = true
      }
      var docEl = document.documentElement
      var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
      var recalc = function () {
        let that1 = this
        var clientWidth = docEl.clientWidth
        if (!clientWidth) return
        if (clientWidth < 1050) {
          that1.loginImgShow = false
        } else {
          that1.loginImgShow = true
        }
      }
      if (!document.addEventListener) return
      window.addEventListener(resizeEvt, recalc, false)
      document.addEventListener('DOMContentLoaded', recalc, false)
    }
  }
}
</script>

<style lang="stylus" scoped>
.login
  width 100%
  height 100vh
  background-image url("../../assets/img/login/bg.png")
  background-repeat no-repeat
  background-size 100% 100%
  display table
  .login-warp
    display table-cell
    vertical-align middle
    .login-form
      width 84.58%
      min-width 960px
      height 89.63%
      min-height 600px
      background-image url("../../assets/img/login/bg2.png")
      background-repeat no-repeat
      background-size 100% 100%
      background-position left top
      border-radius 10px
      text-align center
      margin 0 auto
      padding-top 60px
      position relative
      .login-form-img
        display block
        width 43.22%
        position absolute
        left 8.06%
        top 26.19%
      .login-form-in
        width 48%
        height 100%
        position absolute
        right 0
        top 0
        .login-form-in-tit
          display block
          font-size 36px
          color #128bf1
          padding-top 30%
          margin-bottom 8%
        .login-form-item
          display block
          margin-bottom 40px
          .login-form-item-input
            width 400px
            height 50px
          .login-form-item-submit
            margin-top 10%
            width 320px
            height 40px
</style>



